////
/// Content
/// @group base
////
html {
    @include font-family('default');
    font-feature-settings: 'lnum' 0;
    font-size: $base-font-size;
    font-style: normal;
    font-weight: $font-weight-normal;
    line-height: $base-font-size;
    text-rendering: optimizeLegibility;

    @include media('<=small') {
        font-size: $small-font-size;
        line-height: $small-font-size;
    }
}

body {
}

u {
    text-decoration: none;
}

.fal {
    @include font-family('icon');
    font-weight: $font-weight-light;
}

.far {
    @include font-family('icon');
    font-weight: $font-weight-normal;
}

.fa,
.fas {
    @include font-family('icon');
    font-weight: $font-weight-extrablack;
}

.fab {
    @include font-family('brands');
    font-weight: $font-weight-normal;
}

.sans {
    @include font-family('default');
    font-weight: $font-weight-normal;

    b,
    strong {
        font-weight: $font-weight-bold;
    }

    i,
    em {
        font-style: italic;
    }
}

.serif {
    @include font-family('serif');
    font-weight: $font-weight-normal;

    b,
    strong {
        font-weight: $font-weight-bold;
    }

    i,
    em {
        font-style: italic;
    }
}

.mono {
    @include font-family('mono');
    font-weight: $font-weight-normal;

    b,
    strong {
        font-weight: $font-weight-bold;
    }

    i,
    em {
        font-style: normal;
    }
}

.small {
    @include font-family('small');
    font-weight: $font-weight-normal;

    b,
    strong {
        font-weight: $font-weight-bold;
    }

    i,
    em {
        font-style: normal;
    }
}

.meta {
    @include font-family('heading');
    font-weight: $font-weight-normal;

    b,
    strong {
        font-weight: $font-weight-bold;
    }

    i,
    em {
        font-style: italic;
    }
}

.weight-hairline {
    font-weight: $font-weight-hairline;
}

.weight-thin {
    font-weight: $font-weight-thin;
}

.weight-light {
    font-weight: $font-weight-light;
}

.weight-normal {
    font-weight: $font-weight-normal;
}

.weight-book {
    font-weight: $font-weight-book;
}

.weight-medium {
    font-weight: $font-weight-medium;
}

.weight-bold {
    font-weight: $font-weight-bold;
}

.weight-black {
    font-weight: $font-weight-black;
}

.weight-extrablack {
    font-weight: $font-weight-extrablack;
}

%content-style {
    @include font-family('default');
    @extend %alphatype;
    font-style: normal;
    font-weight: $font-weight-normal;
    max-width: $max-line-length;

    b,
    strong {
        font-weight: $font-weight-bold;
    }

    i,
    em {
        font-style: italic;
    }
}

main {
    p {
        @extend %content-style;

        & + p,
        & + ol,
        & + ul,
        & + dl {
            margin-top: $measure-alpha;
        }
    }

    .standfirst,
    %standfirst {
        @include font-family('heading');
        @extend %gammatype;
        font-style: normal;
        font-weight: $font-weight-light;
        max-width: $max-line-length;
    }

    ol,
    ul,
    dl {
        margin-left: $measure-zeta;
        max-width: $max-line-length;
    }

    ul {
        list-style: disc;

        ul {
            list-style: circle;
        }
    }

    li {
        @extend %content-style;

        & + li {
            margin-top: $measure-alpha;
        }
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        @include font-family('heading');
        font-feature-settings: 'lnum' 1;

        b,
        strong {
            font-weight: inherit;
        }

        u {
            text-decoration: none;
        }
    }

    h6,
    %h6style {
        @extend %alphatype;
        font-style: normal;
        font-weight: $font-weight-bold;
        margin-top: $measure-alpha;
    }

    h5,
    %h5style {
        @extend %betatype;
        font-style: normal;
        font-weight: $font-weight-medium;
        margin-top: $measure-beta;
    }

    h4,
    %h4style {
        @extend %gammatype;
        font-style: normal;
        font-weight: $font-weight-medium;
        margin-top: $measure-gamma;
    }

    h3,
    %h3style {
        @extend %deltatype;
        font-style: normal;
        font-weight: $font-weight-medium;
        margin-top: $measure-delta;
    }

    h2,
    %h2style {
        @extend %epsilontype;
        font-style: normal;
        font-weight: $font-weight-medium;
        margin-top: $measure-epsilon;
    }

    h1,
    %h1style {
        @extend %zetatype;
        font-style: normal;
        font-weight: $font-weight-medium;
    }
}
