////
/// Variables
/// @group abstracts
////
/// @type Length
$width-max: 1440px !default;
/// @type Length
$width-max-tablet: 1280px !default;
/// @type Length
$width-max-between: 900px !default;
/// @type Length
$width-max-mobile: 600px !default;
/// @type Length
$width-max-small: 400px !default;

$breakpoints: (
    'small': $width-max-small,
    'mobile': $width-max-mobile,
    'between': $width-max-between,
    'tablet': $width-max-tablet,
    'max': $width-max,
) !default;

/// @type String
$font-path: '/fonts/' !default;
/// @type Length
$transition-duration: 0.3s !default;
/// @type Length
$transition-duration-long: 0.6s !default;
/// @type String
$transition-function: ease !default;

/// @type Number
$measure-ratio: 9/8 !default;
/// @type Length
$measure-basic: 1rem !default;
/// @type Length
$measure-alpha: $measure-basic !default;
/// @type Length
$measure-beta: decimal-round(($measure-alpha * $measure-ratio), 1) !default;
/// @type Length
$measure-gamma: decimal-round(($measure-beta * $measure-ratio), 1) !default;
/// @type Length
$measure-delta: decimal-round(($measure-gamma * $measure-ratio), 1) !default;
/// @type Length
$measure-epsilon: decimal-round(($measure-delta * $measure-ratio), 1) !default;
/// @type Length
$measure-zeta: decimal-round(($measure-epsilon * $measure-ratio), 1) !default;
/// @type Length
$measure-small: decimal-round(($measure-alpha / $measure-ratio), 1) !default;
/// @type Length
$measure-extrasmall: decimal-round(
    ($measure-small / $measure-ratio),
    1
) !default;

/// @type Length
$border-width-hairline: 1px !default;
/// @type Length
$border-width-default: 2px !default;
/// @type Length
$border-width-thick: 3px !default;
/// @type Length
$border-width-thicker: 4px !default;

/// @type String
$border-style-solid: solid !default;
/// @type String
$border-style-dotted: dotted !default;

/// @type Number
$lightness-percentage: 30% !default;
