////
/// Breadcrumbs
/// @group components
////
.breadcrumbs {
    display: grid;
    margin-top: $measure-extrasmall;

    ol {
        float: left;
        list-style: none;
        width: 100%;

        li {
            @include font-family('small');
            @extend %smalltype;
            float: left;
            font-weight: normal;
            margin-bottom: $measure-extrasmall;

            &:not(:last-of-type) {
                margin-right: $measure-extrasmall;

                &:after {
                    @include font-family('icon');
                    content: '\f105';
                    font-weight: $font-weight-light;
                    margin-left: $measure-extrasmall;
                }
            }
        }
    }
}
