////
/// Anchor mixins
/// @group abstracts
////
@mixin anchor-reset() {
    background-color: inherit;
    border-bottom-width: 0;
    color: inherit;
    cursor: pointer;

    &:hover,
    &:focus,
    &:active {
        background-color: inherit;
        color: inherit;
        outline-width: 0;
    }
}

@mixin anchor-inline-dark() {
    @include anchor-reset();

    background: unset;
    border-bottom-color: $color-inline-link;
    border-bottom-style: $border-style-solid;
    border-bottom-width: $border-width-default;
    color: $color-inline-link;
    text-decoration: none;

    &:focus {
        background: unset;
        color: $color-inline-link;
        outline-color: $color-darktext;
        outline-width: $border-width-default;
        outline-style: $border-style-dotted;
        outline-offset: $border-width-thick;
    }

    &:hover {
        background: $color-inline-link;
        color: $color-purewhite;
        outline: none;
    }

    &:active {
        background: $color-inline-link;
        color: $color-purewhite;
        outline-color: $color-darktext;
        outline-width: $border-width-default;
        outline-style: $border-style-dotted;
        outline-offset: $border-width-thick;
    }
}

@mixin anchor-inline-light() {
    @include anchor-reset();

    color: $color-inline-link;
    border-bottom-color: $color-inline-link;
    border-bottom-width: $border-width-default;
    border-bottom-style: $border-style-solid;
    text-decoration: none;

    &:hover {
        color: lighten($color-inline-link, $lightness-percentage);
        border-bottom-color: lighten($color-inline-link, $lightness-percentage);
        outline: none;
    }

    &:focus {
        color: $color-inline-link;
        border-bottom-color: $color-inline-link;
        outline-color: $color-lighttext;
        outline-width: $border-width-default;
        outline-style: $border-style-dotted;
        outline-offset: $border-width-thick;
    }

    &:active {
        color: lighten($color-inline-link, $lightness-percentage);
        border-bottom-color: lighten($color-inline-link, $lightness-percentage);
        outline-color: $color-lighttext;
        outline-width: $border-width-default;
        outline-style: $border-style-dotted;
        outline-offset: $border-width-thick;
    }
}

@mixin anchor-img() {
    @include anchor-reset() color: $color-inline-link;
    text-decoration: none;

    &:hover {
        color: darken($color-inline-link, $lightness-percentage);
        outline: none;
    }

    &:focus {
        color: $color-inline-link;
        outline-color: $color-darktext;
        outline-width: $border-width-default;
        outline-style: $border-style-dotted;
        outline-offset: $border-width-thick;
    }

    &:active {
        color: darken($color-inline-link, $lightness-percentage);
        outline-color: $color-darktext;
        outline-width: $border-width-default;
        outline-style: $border-style-dotted;
        outline-offset: $border-width-thick;
    }
}
