////
/// Page
/// @group base
////
html {
    background-color: $color-lightbg;
    color: $color-darktext;
    box-sizing: border-box;
    min-height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    position: relative;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    background-color: $color-lightbg;
    color: $color-darktext;
}
