////
/// CMS editor warning
/// @group components
////
.cms-editor-warning {
    display: none;
}

@mixin cms-warning() {
    background: darkred !important;
    color: $color-purewhite !important;
    font-weight: $font-weight-bold !important;
    padding-left: $measure-small !important;
    padding-right: $measure-small !important;
}

.cms-editor {
    div.cms-editor-warning {
        display: block;

        p {
            @include cms-warning();
            @extend %alphatype;
            margin-bottom: $measure-small !important;
            max-width: $max-line-length;
            padding-top: $measure-small !important;
            padding-bottom: calc(#{$measure-alpha} * 3) !important;
            position: relative !important;

            &:after {
                @include font-family('small');
                @extend %extrasmalltype;
                bottom: $measure-small !important;
                content: 'CMS editor warning' !important;
                font-weight: $font-weight-normal !important;
                position: absolute !important;
                right: $measure-small !important;
            }
        }
    }

    span.cms-editor-warning {
        @include cms-warning();
        display: inline;
    }
}
