// ////
/// Fonts
/// @group base
////
@import '../../node_modules/@fortawesome/fontawesome-pro/scss/variables';
@import '../../node_modules/@fortawesome/fontawesome-pro/scss/mixins';
@import '../../node_modules/@fortawesome/fontawesome-pro/scss/core';
@import '../../node_modules/@fortawesome/fontawesome-pro/scss/larger';
@import '../../node_modules/@fortawesome/fontawesome-pro/scss/fixed-width';
@import '../../node_modules/@fortawesome/fontawesome-pro/scss/list';
@import '../../node_modules/@fortawesome/fontawesome-pro/scss/bordered-pulled';
@import '../../node_modules/@fortawesome/fontawesome-pro/scss/animated';
@import '../../node_modules/@fortawesome/fontawesome-pro/scss/rotated-flipped';
@import '../../node_modules/@fortawesome/fontawesome-pro/scss/stacked';
@import '../../node_modules/@fortawesome/fontawesome-pro/scss/icons';
@import '../../node_modules/@fortawesome/fontawesome-pro/scss/screen-reader';

/// @type Map
/// @prop {String} family - Font Family
/// @prop {String} name - Font Name
/// @prop {String} style - Font Style
/// @prop {Number} weight - Font Weight
/// @prop {String} filename - Font Filepath and Filename
$font-families-styles: (
    'FF Meta': (
        ('Hairline', normal, 100, 'ff-meta/ff-meta-w03-hairline'),
        ('Thin', normal, 200, 'ff-meta/ff-meta-w03-thin'),
        ('Light', normal, 300, 'ff-meta/ff-meta-w03-light'),
        ('Normal', normal, 400, 'ff-meta/ff-meta-w03-normal'),
        ('Book', normal, 500, 'ff-meta/ff-meta-w03-book'),
        ('Medium', normal, 600, 'ff-meta/ff-meta-w03-medium'),
        ('Bold', normal, 700, 'ff-meta/ff-meta-w03-bold'),
        ('Black', normal, 800, 'ff-meta/ff-meta-w03-black'),
        ('Hairline Italic', italic, 100, 'ff-meta/ff-meta-w03-hairline-italic'),
        ('Thin Italic', italic, 200, 'ff-meta/ff-meta-w03-thin-italic'),
        ('Light Italic', italic, 300, 'ff-meta/ff-meta-w03-light-italic'),
        ('Normal Italic', italic, 400, 'ff-meta/ff-meta-w03-normal-italic'),
        ('Book Italic', italic, 500, 'ff-meta/ff-meta-w03-book-italic'),
        ('Medium Italic', italic, 600, 'ff-meta/ff-meta-w03-medium-italic'),
        ('Bold Italic', italic, 700, 'ff-meta/ff-meta-w03-bold-italic'),
        ('Black Italic', italic, 800, 'ff-meta/ff-meta-w03-black-italic'),
    ),
    'PT Sans': (
        ('Regular', normal, 400, 'ptsansweb/PTS55F_W'),
        ('Bold', normal, 700, 'ptsansweb/PTS75F_W'),
        ('Italic', italic, 400, 'ptsansweb/PTS56F_W'),
        ('Bold Italic', italic, 700, 'ptsansweb/PTS76F_W'),
    ),
    'PT Caption': (
        ('Regular', normal, 400, 'ptsansweb/PTC55F_W'),
        ('Bold', normal, 700, 'ptsansweb/PTC75F_W'),
    ),
    'PT Serif': (
        ('Regular', normal, 400, 'ptserifweb/PTF55F_W'),
        ('Bold', normal, 700, 'ptserifweb/PTF75F_W'),
        ('Italic', italic, 400, 'ptserifweb/PTF56F_W'),
        ('Bold Italic', italic, 700, 'ptserifweb/PTF76F_W'),
    ),
    'PT Mono': (
        ('Regular', normal, 400, 'ptmonoweb/PTM55F_W'),
        ('Bold', normal, 700, 'ptmonoweb/PTM75F_W'),
    ),
    'Font Awesome': (
        ('Light', normal, 300, 'fontawesome/fa-light-300'),
        ('Regular', normal, 400, 'fontawesome/fa-regular-400'),
        ('Solid', normal, 900, 'fontawesome/fa-solid-900'),
    ),
    'Font Awesome Brands': (
        ('Regular', normal, 400, 'fontawesome/fa-brands-400'),
    ),
);

/// @type Map
/// @prop {String} family - Font Family
/// @prop {List} types - Font File Types
$font-families-types: (
    'FF Meta': (
        woff2,
        woff,
    ),
    'PT Sans': (
        woff2,
        woff,
    ),
    'PT Caption': (
        woff2,
        woff,
    ),
    'PT Serif': (
        woff2,
        woff,
    ),
    'PT Mono': (
        woff2,
        woff,
    ),
    'Font Awesome': (
        woff2,
        woff,
    ),
    'Font Awesome Brands': (
        woff2,
        woff,
    ),
);

@each $family, $styles in $font-families-styles {
    @each $style in $styles {
        $src: ();
        $src: append($src, local('#{$family} #{nth($style, 1)}'));
        @each $type in map-get($font-families-types, $family) {
            $url: asset-url('#{$font-path}#{nth($style, 4)}.#{$type}')
                format('#{$type}');
            $src: append($src, $url, comma);
        }

        @font-face {
            font-family: $family;
            font-style: nth($style, 2);
            font-weight: nth($style, 3);
            font-display: swap;
            src: #{$src};
        }
    }
}

/// @type List
$font-family-meta: 'FF Meta', sans-serif !default;
/// @type List
$font-family-sans: 'PT Sans', sans-serif !default;
/// @type List
$font-family-caption: 'PT Caption', sans-serif !default;
/// @type List
$font-family-serif: 'PT Serif', serif !default;
/// @type List
$font-family-mono: 'PT Mono', monospace !default;
/// @type List
$font-family-icon: 'Font Awesome' !default;
/// @type List
$font-family-brands: 'Font Awesome Brands' !default;
/// @type List
$font-family-head: $font-family-meta !default;
/// @type List
$font-family-body: $font-family-sans !default;

/// @type Map
/// @prop {String} key - Font Family name
/// @prop {Number} value - Font Family value
/// @require {Variable} $font-family-body
/// @require {Variable} $font-family-head
/// @require {Variable} $font-family-serif
/// @require {Variable} $font-family-icon
/// @require {Variable} $font-family-small
/// @require {Variable} $font-family-mono
/// @see {Mixin} font-family
$font-families: (
    'default': $font-family-body,
    'heading': $font-family-head,
    'serif': $font-family-serif,
    'mono': $font-family-mono,
    'small': $font-family-caption,
    'icon': $font-family-icon,
    'brands': $font-family-brands,
) !default;

/// @type Length
$base-font-size: 18px !default;
/// @type Length
$small-font-size: 16px !default;

/// @type Length
$max-line-length: 60ch !default;

/// @type Number
$font-weight-hairline: 100 !default;
$font-weight-thin: 200 !default;
$font-weight-light: 300 !default;
$font-weight-normal: 400 !default;
$font-weight-book: 500 !default;
$font-weight-medium: 600 !default;
$font-weight-bold: 700 !default;
$font-weight-black: 800 !default;
$font-weight-extrablack: 900 !default;

/// @type Length
$font-size-basic: 1rem !default;

/// @type Number
$font-size-ratio: 9/8 !default;
/// @type Length
$font-size-alpha: $font-size-basic !default;
/// @type Length
$font-size-beta: decimal-round(
    ($font-size-alpha * $font-size-ratio),
    1
) !default;
/// @type Length
$font-size-gamma: decimal-round(
    ($font-size-beta * $font-size-ratio),
    1
) !default;
/// @type Length
$font-size-delta: decimal-round(
    ($font-size-gamma * $font-size-ratio),
    1
) !default;
/// @type Length
$font-size-epsilon: decimal-round(
    ($font-size-delta * $font-size-ratio),
    1
) !default;
/// @type Length
$font-size-zeta: decimal-round(
    ($font-size-epsilon * $font-size-ratio),
    1
) !default;
/// @type Length
$font-size-smalltype: decimal-round(
    ($font-size-alpha / $font-size-ratio),
    1
) !default;
/// @type Length
$font-size-extrasmalltype: decimal-round(
    ($font-size-smalltype / $font-size-ratio),
    1
) !default;

/// @type Number
$line-height-ratio: 5/4 !default;
/// @type Length
$line-height-addition: $font-size-basic / 8 !default;
/// @type Length
$line-height-alpha: ($font-size-alpha * $line-height-ratio) +
    $line-height-addition !default;
/// @type Length
$line-height-beta: ($font-size-beta * $line-height-ratio) +
    $line-height-addition !default;
/// @type Length
$line-height-gamma: ($font-size-gamma * $line-height-ratio) +
    $line-height-addition !default;
/// @type Length
$line-height-delta: ($font-size-delta * $line-height-ratio) +
    $line-height-addition !default;
/// @type Length
$line-height-epsilon: ($font-size-epsilon * $line-height-ratio) +
    $line-height-addition !default;
/// @type Length
$line-height-zeta: ($font-size-zeta * $line-height-ratio) +
    $line-height-addition !default;
/// @type Length
$line-height-smalltype: ($font-size-smalltype * $line-height-ratio) +
    $line-height-addition !default;
/// @type Length
$line-height-extrasmalltype: ($font-size-extrasmalltype * $line-height-ratio) +
    $line-height-addition !default;

%alphatype {
    font-size: $font-size-alpha;
    line-height: $line-height-alpha;
}

%betatype {
    font-size: $font-size-beta;
    line-height: $line-height-beta;
}

%gammatype {
    font-size: $font-size-gamma;
    line-height: $line-height-gamma;
}

%deltatype {
    font-size: $font-size-delta;
    line-height: $line-height-delta;
}

%epsilontype {
    font-size: $font-size-epsilon;
    line-height: $line-height-epsilon;
}

%zetatype {
    font-size: $font-size-zeta;
    line-height: $line-height-zeta;
}

%smalltype {
    font-size: $font-size-smalltype;
    line-height: $line-height-smalltype;
}

%extrasmalltype {
    font-size: $font-size-extrasmalltype;
    line-height: $line-height-extrasmalltype;
}

///
/// Specify font-family
///
/// @param {String} $family - Font-family value
///
/// @require {Map} $font-families
///
/// @example scss - One family
///   @include font-family(default);
///   // font-family: "Proxima Nova", sans-serif;
///
/// @throw Error if no parameters are passed.
/// @throw Error if parameter passed does not match a key in $font-families.
///
@mixin font-family($family) {
    @if not $family {
        @error "font-family() expects one parameter.";
    }

    @if not map-has-key($font-families, $family) {
        @error "There is no `font-family` named `#{$family}` in `$font-families`. `font-family` should be one of `#{map-keys($font-families)}`.";
    }

    font-family: map-get($font-families, $family);
}
